<template>
	<TremendousCard class="mb-6" />
  <div
    class="grid grid-cols-12 gap-6 rounded-lg"
    v-if="!isEmptyPayoutMethods"
  >
    <div
      class="lg:col-span-5 col-span-12 border rounded-lg border-grey-01 p-2 pt-6 relative"
    >
    <p class="mb-3 absolute -top-3 -translate-y-1/2 bg-white px-2">Payout</p>
      <p class="mb-3 font-semibold ml-3">Choose Payout Method</p>
      <!--      <Tab color="teal" v-model="paymentMethod" :by="null" :items="items" :validate="validate" component type="vertical" />-->
      <Tab
        color="teal"
        v-model="paymentMethod"
        :by="null"
        :items="items"
        align="left"
        :use-external-icon="true"
        component
        type="vertical"
      />
    </div>
    <div class="lg:col-span-7 col-span-12">
      <template v-if="paymentMethod.is_custom">
        <component
          ref="refChildComponent"
          v-model:data="data"
          :payout="paymentMethod"
          :is="paymentMethod.component"
        />
      </template>
      <template v-else>
        <component
          v-bind="bind"
          v-model:data="data"
          :is="paymentMethod.component"
          ref="refChildComponent"
        />
      </template>
      <slot name="additional" />
    </div>
  </div>
  <div class="flex items-center justify-center flex-col my-10" v-else>
    <p class="text-grey-04 mb-4 text-center">
      Oops, you haven't set up payout for your customers. <br />
      Please click the button below
    </p>
    <Button
      color="teal"
      :to="{ name: 'settingPayoutsMethod' }"
      link
      dense
      >Payout Methods</Button
    >
  </div>
</template>

<script>
import { defineComponent, ref, computed, reactive, watch } from 'vue'
import Tab from 'bbUi/components/tab/tab.vue'
import AmazonGiftCard from './PaymentMethod/AmazonGiftCard.vue'
import BankTransfer from './PaymentMethod/BankTransfer.vue'
import PaperCheck from './PaymentMethod/PaperCheck.vue'
import ExpressCheck from './PaymentMethod/PaperCheck.vue'
import PayPal from './PaymentMethod/PayPal.vue'
import Swish from './PaymentMethod/Swish.vue'
import SquareCash from './PaymentMethod/SquareCash.vue'
import Venmo from './PaymentMethod/Venmo.vue'
import Coinbase from './PaymentMethod/Coinbase.vue'
import Zelle from './PaymentMethod/Zelle.vue'
import CashApp from './PaymentMethod/CashApp.vue'
import ApplePay from './PaymentMethod/ApplePay.vue'
import GooglePay from './PaymentMethod/GooglePay.vue'
import FacebookPay from './PaymentMethod/FacebookPay.vue'
import Cash from './PaymentMethod/Cash.vue'
import StoreCredit from './PaymentMethod/StoreCredit.vue'
import ChooseLater from './PaymentMethod/ChooseLater.vue'
import VirtualDebitCard from './PaymentMethod/VirtualDebitCard.vue'
import GiftCard from './PaymentMethod/GiftCard.vue'
import InteraceTransfer from './PaymentMethod/InteracTransfer.vue'
import Echeck from './PaymentMethod/Echeck.vue'
import Donate from './PaymentMethod/Donate.vue'
import Bill from './PaymentMethod/Bill.vue'
import Rebate from './PaymentMethod/Rebate.vue'
import Custom from './PaymentMethod/Custom.vue'
import Default from './PaymentMethod/Default.vue'
import Button from 'bbUi/components/button/button.vue'
import TremendousCard from "components/Shared/TremendousCard/TremendousCard.vue";
import Revolut from './PaymentMethod/Revolut.vue'

import { PAYMENT_ID } from '@/constants/payment.js'

export default defineComponent({
  components: {
    Tab,
    AmazonGiftCard,
    BankTransfer,
    PaperCheck,
    ExpressCheck,
    PayPal,
    Swish,
    SquareCash,
    Venmo,
    Coinbase,
    Cash,
    Zelle,
    CashApp,
    ApplePay,
    GooglePay,
    FacebookPay,
    StoreCredit,
		ChooseLater,
    VirtualDebitCard,
    GiftCard,
    InteraceTransfer,
    Echeck,
    Donate,
    Bill,
    Rebate,
    Custom,
    Default,
    Button,
		TremendousCard,
    Revolut
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    },
    items: {
      type: Array,
      default: () => ([])
    },
    autoSelect: {
      type: Boolean,
      default: false
    },
    showTip: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'update:data'
  ],
  setup(props, { emit }) {
    const refChildComponent = ref(null)
    const paymentMethod = ref(null)
    const data = ref(null)
    const tempData = reactive(props.data || {})
    const find = props.data ? props.items.findIndex(item => item.name === tempData.payment_method) : -1
    const index = find !== -1 ? find : 0

    if( props.autoSelect ){
      paymentMethod.value = props.items[index] ? props.items[index] : null
    }else{
      paymentMethod.value = {
        component: 'default'
      }
    }
    const isEmptyPayoutMethods = computed(() => Object.keys(props.items).length < 1)

    watch(paymentMethod, () => {
      let temp

      if(props.data && find !== -1 && props.items[index].name === paymentMethod.value.name) {
        temp = tempData
      }
      else if (paymentMethod.value && !paymentMethod.value.attributes?.length && paymentMethod.value.name !== 'Bank Transfer'){
        temp = paymentMethod.value.attributes
      }
      else if(paymentMethod.value && paymentMethod.value.default_value !== '-' && !paymentMethod.value.default_value?.length) {
        temp = paymentMethod.value.default_value
      }
      else {
        temp = {}
      }

      data.value = Object.assign({
        payment_method: paymentMethod.value?.name || null
      }, temp)
      emit('update:data', data.value)
    }, {
      immediate: true
    })
    const list = computed(() => {
      if(paymentMethod.value && paymentMethod.value.default_value !== '-' && paymentMethod.value.default_value?.length) {
        return paymentMethod.value.default_value
      }
      return null
    })
    const bind = computed(() => {
      const bind = {}
      // data.value = Object.assign({
      //   payment_method: paymentMethod.value.name
      // }, data.value)
      if(list.value) bind.list = list.value

      // to show and remove arrow on cash payout method component
      const isEmptyFormPayoutIds = [PAYMENT_ID.CASH, PAYMENT_ID.STORE_CREDIT, PAYMENT_ID.VIRTUAL_DEBIT_CARD, PAYMENT_ID.GIFT_CARD]
      if(isEmptyFormPayoutIds.includes(paymentMethod.value?.id) && props.showTip) Object.assign(bind, {showTip: true})

      const isBankTransfer = paymentMethod.value?.id === PAYMENT_ID.BANK_TRANSFER && paymentMethod.value?.name === 'Bank Transfer'
      if(isBankTransfer) {
        const hasPredefinedAttributes = props.data?.bank_transfer
        if(hasPredefinedAttributes){
          bind.attributes = props.data.bank_transfer
          bind.defaultAttributes = paymentMethod.value?.attributes
        }else{
          bind.defaultAttributes = paymentMethod.value?.attributes
        }
      }

      // emit('update:data', bind.data)
      return bind
    })
    watch(() => ({...data.value}), () => {
      emit('update:data', data.value)
    },{
      immediate: true
    })
    const validate = data => {
      return data.default_value !== '-'
    }

    return {
      paymentMethod,
      isEmptyPayoutMethods,
      bind,
      data,
      refChildComponent,
      validate
    }
  }
})
</script>
