<script>
import { defineComponent, reactive, watch } from 'vue'
import Input from 'bbUi/components/input/input.vue'
import { useVuelidate } from '@vuelidate/core'

export default defineComponent({
  components: {
    Input
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    },
    disabled: {
      type: Boolean,
      default: false
    },
    showTip: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:data'],
  setup(props, { emit }) {
    const data = reactive({ ...props.data })
    const rules = {}
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    watch(
      () => ({ ...data }),
      () => {
        emit('update:data', data)
      }
    )

    return {
      data,
      validation,
      validate
    }
  }
})
</script>
